/**
 * Roll - Core roll logic (positioning, animations)
 */
class Roll {
  constructor(element, eventManager, config) {
    this.element = element;
    this.eventManager = eventManager;
    this.config = config;
    this.currentIndex = 0;
    this.container = null;
    this.viewport = null;
    this.itemElements = [];
    this.isAnimating = false;
    this.transitionId = null;
    
    // For seamless infinite loop
    this.realItemCount = 0; // Count of actual items (excluding clones)
    this.hasClones = false; // Whether clone items are added
  }

  /**
   * Initialize roll DOM structure
   */
  initialize() {
    this.setupDOM();
    this.setupStyles();
  }

  /**
   * Setup roll DOM structure
   */
  setupDOM() {
    // Create viewport container
    this.viewport = document.createElement('div');
    this.viewport.className = 'swr-viewport';

    // Create scrollable container
    this.container = document.createElement('div');
    this.container.className = 'swr-container';

    // Move existing items to container
    const existingItems = this.element.querySelectorAll('[data-swr-item]');
    const itemArray = Array.from(existingItems);
    
    this.realItemCount = itemArray.length;
    
    // For seamless infinite loop, add clones if loop is enabled and we have items
    if (this.config.loop && this.realItemCount > 0) {
      this.hasClones = true;
      
      // Clone last item and add at the beginning (for prev from first item)
      const lastClone = itemArray[this.realItemCount - 1].cloneNode(true);
      lastClone.setAttribute('data-swr-clone', 'last');
      this.container.appendChild(lastClone);
    }
    
    // Add all real items
    itemArray.forEach((item) => {
      this.container.appendChild(item.cloneNode(true));
    });
    
    // Add clone of first item at the end (for next from last item)
    if (this.config.loop && this.realItemCount > 0) {
      const firstClone = itemArray[0].cloneNode(true);
      firstClone.setAttribute('data-swr-clone', 'first');
      this.container.appendChild(firstClone);
    }

    this.viewport.appendChild(this.container);
    this.element.innerHTML = '';
    this.element.appendChild(this.viewport);

    // Add SWR class to main element
    this.element.classList.add('swr');

    // Update item elements reference
    this.updateItemElements();
    
    // Start at the first real item (index 1 if we have clones)
    if (this.hasClones) {
      this.currentIndex = 1; // Start at first real item (after last clone)
      // Position container without animation
      const offset = -this.currentIndex * 100;
      this.container.style.transition = 'none';
      this.container.style.transform = `translateY(${offset}%)`;
      // Re-enable transitions after a frame
      requestAnimationFrame(() => {
        this.container.style.transition = `transform ${this.config.transitionDuration}ms ease-in-out`;
      });
    }
  }

  /**
   * Setup initial styles
   */
  setupStyles() {
    // Set viewport aspect ratio using padding-bottom trick for better browser support
    const ratio = this.parseAspectRatio(this.config.aspectRatio);
    const paddingPercent = (1 / ratio) * 100;
    
    this.viewport.style.position = 'relative';
    this.viewport.style.width = '100%';
    this.viewport.style.paddingBottom = `${paddingPercent}%`;
    this.viewport.style.overflow = 'hidden';

    // Setup container for vertical scrolling (Instagram Reels style)
    this.container.style.position = 'absolute';
    this.container.style.top = '0';
    this.container.style.left = '0';
    this.container.style.width = '100%';
    this.container.style.height = '100%';
    this.container.style.display = 'flex';
    this.container.style.flexDirection = 'column';
    this.container.style.transition = `transform ${this.config.transitionDuration}ms ease-in-out`;
    this.container.style.transform = 'translateY(0)';
  }

  /**
   * Parse aspect ratio string to numeric value
   * @param {string} ratio - Aspect ratio (e.g., "9:16")
   * @returns {number}
   */
  parseAspectRatio(ratio) {
    const [width, height] = ratio.split(':').map(Number);
    return width / height;
  }

  /**
   * Update item elements reference
   */
  updateItemElements() {
    this.itemElements = Array.from(
      this.container.querySelectorAll('[data-swr-item]')
    );
  }

  /**
   * Add item element to container
   * @param {number} index - Optional index to insert at
   * @returns {HTMLElement} Created item element
   */
  addItemElement(index = null) {
    const itemElement = document.createElement('div');
    itemElement.setAttribute('data-swr-item', '');
    itemElement.style.flex = '0 0 100%';
    itemElement.style.width = '100%';
    itemElement.style.height = '100%';
    itemElement.style.overflow = 'hidden';

    if (index !== null && index >= 0 && index < this.container.children.length) {
      this.container.insertBefore(itemElement, this.container.children[index]);
    } else {
      this.container.appendChild(itemElement);
    }

    this.updateItemElements();
    return itemElement;
  }

  /**
   * Render item at index
   * @param {number} index - Item index
   * @param {Object} renderer - Renderer instance
   * @param {Object} item - Item data
   */
  renderItem(index, renderer, item) {
    // Create item elements until we have enough
    while (index >= this.itemElements.length) {
      this.addItemElement();
    }

    if (index < 0 || index >= this.itemElements.length) {
      console.warn('Invalid item index:', index);
      return;
    }

    this.eventManager.emit('beforeRender', { index, item });

    const element = this.itemElements[index];
    element.innerHTML = '';
    element.appendChild(renderer.render(item));

    this.eventManager.emit('afterRender', { index, item });
  }

  /**
   * Slide to item at index
   * @param {number} targetIndex - Target item index (can be clone index)
   */
  slideTo(targetIndex) {
    if (targetIndex < 0 || targetIndex >= this.itemElements.length) {
      console.warn('Invalid slide index:', targetIndex);
      return;
    }

    if (this.isAnimating) {
      console.log('â³ Animation in progress, ignoring slide request');
      return;
    }

    this.isAnimating = true;
    this.currentIndex = targetIndex;

    const offset = -targetIndex * 100;
    this.container.style.transform = `translateY(${offset}%)`;
    
    console.log('ðŸŽ¬ Sliding to item', targetIndex, '- Transform: translateY(' + offset + '%)');

    // Emit slide event
    this.eventManager.emit('slideStarted', { index: targetIndex });

    // Clear previous timeout
    if (this.transitionId) clearTimeout(this.transitionId);

    // Wait for transition to complete
    this.transitionId = setTimeout(() => {
      this.isAnimating = false;
      console.log('âœ… Slide animation completed for item', targetIndex);
      
      // Check if we're on a clone and need to seamlessly jump to the real item
      if (this.hasClones) {
        const itemElement = this.itemElements[targetIndex];
        const cloneType = itemElement.getAttribute('data-swr-clone');
        
        if (cloneType === 'first') {
          // On first item clone (at the end), jump to real first item
          console.log('ðŸ”„ Seamless jump: Clone of first â†’ Real first item');
          this.instantJumpTo(1); // Index 1 is first real item (after last clone)
        } else if (cloneType === 'last') {
          // On last item clone (at the beginning), jump to real last item
          console.log('ðŸ”„ Seamless jump: Clone of last â†’ Real last item');
          this.instantJumpTo(this.realItemCount); // Last real item index
        }
      }
      
      this.eventManager.emit('slideCompleted', { index: this.currentIndex });
    }, this.config.transitionDuration);
  }

  /**
   * Instantly jump to an index without animation (for seamless looping)
   * @param {number} targetIndex - Target item index
   */
  instantJumpTo(targetIndex) {
    if (targetIndex < 0 || targetIndex >= this.itemElements.length) {
      console.warn('Invalid instant jump index:', targetIndex);
      return;
    }

    this.currentIndex = targetIndex;
    const offset = -targetIndex * 100;
    
    // Disable transition temporarily
    this.container.style.transition = 'none';
    this.container.style.transform = `translateY(${offset}%)`;
    
    console.log('âš¡ Instant jump to item', targetIndex, '- No animation');
    
    // Emit event so Navigation can sync
    this.eventManager.emit('seamlessJump', { index: targetIndex });
    
    // Re-enable transitions after a frame
    requestAnimationFrame(() => {
      this.container.style.transition = `transform ${this.config.transitionDuration}ms ease-in-out`;
    });
  }

  /**
   * Get number of item elements
   * @returns {number}
   */
  getItemCount() {
    return this.itemElements.length;
  }

  /**
   * Get current slide index
   * @returns {number}
   */
  getCurrentIndex() {
    return this.currentIndex;
  }

  /**
   * Destroy roll
   */
  destroy() {
    if (this.transitionId) clearTimeout(this.transitionId);
    this.container = null;
    this.viewport = null;
    this.itemElements = [];
  }
}

if (typeof module !== 'undefined' && module.exports) {
  module.exports = Roll;
}
